@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Poppins', sans-serif;
}

.container {
  max-width: 500px;
  margin: 30px auto;
  overflow: auto;
  min-height: 300px;
  border: 1px solid steelblue;
  padding: 30px;
  border-radius: 5px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.btn {
  display: inline-block;
  background: #000;
  color: #fff;
  border: none;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  font-size: 15px;
  font-family: inherit;
}

.btn:focus {
  outline: none;
}

.btn:active {
  -webkit-transform: scale(0.98);
          transform: scale(0.98);
}

.btn-block {
  display: block;
  width: 100%;
}

.task {
  background: #f4f4f4;
  margin: 5px;
  padding: 10px 20px;
  cursor: pointer;
}

.task.reminder {
  border-left: 5px solid green;
}

.task h3 {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.add-form {
  margin-bottom: 40px;
}

.form-control {
  margin: 20px 0;
}

.form-control label {
  display: block;
}

.form-control input {
  width: 100%;
  height: 40px;
  margin: 5px;
  padding: 3px 7px;
  font-size: 17px;
}

.form-control-check {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.form-control-check label {
  flex: 1 1;
}

.form-control-check input {
  flex: 2 1;
  height: 20px;
}

footer {
  margin-top: 30px;
  text-align: center;
}

/*font-family: 'Montserrat', sans-serif;*/ /*font-family: 'Lato', sans-serif;*/ /*font-family: 'Open Sans', sans-serif;*/ /*font-family: 'Poppins', sans-serif;*/

.header {
    font-family: 'Open Sans', sans-serif;
    font-size: 46px;
}

.subheader {
    font-family: 'Poppins', sans-serif;
    font-size: 28px;
}

.body {
    font-family: 'Lato', sans-serif;
    font-size: 20px;
}

.nav-styling {
    font-family: 'Poppins', sans-serif;
    color: #282c34 !important;
}

a.dropdown-toggle.nav-link {
    font-family: 'Poppins', sans-serif;
    color: #282c34 !important;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.dropdown-menu.show { /* By default there is space between .dropdown-toggle and .dropdown-menu */
  margin: 0px;
}

.navbar-brand { /* By default .navbar-brand has 1rem */
  margin-right: 1rem !important;
}

hr { /* Have to make own hr in Reactstrap */
    border: 0;
    clear:both;
    display:block;
    width: 75%;
    background-color:#FFFFFF;
    height: 1px;
}

.background-blue { /* Used by Footer */
    background-color: #add8e6;
}

.insta-grad svg *{
    fill: url(#rg);
}

.jumbotron {
    background-image: url("https://thumbs.dreamstime.com/z/driving-empty-road-dawn-to-sun-33737304.jpg");
    background-repeat: no-repeat;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}



